import React, { Component, Fragment } from 'react';
import { BiUpArrowAlt } from 'react-icons/bi';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import WOW from 'wowjs';
import Header from './Header';
import Home from './Home';
import About from './About';
import Services from './Services';
import Portfolio from './Portfolio';
import Contact from './Contact';
import Footer from './Footer';
import ContactForm from './ConatctForm';
import WorkingProcess from './WorkingProcess';
import Logo from '../../assets/img/final-dizi-logo3.png';


class HomeContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            backToTop: false
        }
    }
    componentDidMount() {
        window.addEventListener('scroll', this.toggleVisible);
        new WOW.WOW({
            mobile: false
        }).init();
    }

    toggleVisible = () => {
        const scrolled = document.documentElement.scrollTop;
        if (scrolled > 600) {
            this.setState({ backToTop: true })
        }
        else if (scrolled <= 600) {
            this.setState({ backToTop: false })
        }
    };

    scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
            /* you can also use 'auto' behaviour
               in place of 'smooth' */
        });
    };

    render() {
        return (
            <Fragment>
                <Header history={this.props.history } />  
                <div style={{ backgroundColor: "#e6e7ee" }}>
                    <Home />
                    <About />
                    <Services />
                    <Portfolio />
                    <WorkingProcess />
                    <Contact />
                    <ContactForm />
                </div>
                <Footer />
                <div className={`go-top ${this.state.backToTop ? ' active' : ''}`} onClick={this.scrollToTop}>
                    <BiUpArrowAlt />
                </div>
            </Fragment>
        )
    }
}

export default HomeContainer